import css from "../../styles/video.module.scss";
const VIDEO = () => {
    return (
        <div className={css.container}>
            <div className={css.title}>
                <p><b>CUBE MEDIA</b> OFERTA VIDEO</p>
            </div>
            <div className={css.mainText}>
            
                <p> Cube media oferuje szeroki zakres usug z zakresu realizacji oraz 
                    montau filmw na wszelkie social media. </p>
                <span className={css.pricesText}>
                <p>  Film z eventu muzycznego do 10 min 1000z </p>
                <p>  Nagranie koncertu do 60 min 1500z </p>
                <p>  Spot reklamowy dla firmy do 7 min 2500z </p>
                <p>  Teledysk muzyczny od 1500z </p>
                <p>  Spot z wydarzenia od 1400z za 15 min film </p>
                <p>  Wywiad na social media 750z za 5min </p>
                </span>    
                
                    
                <p className={css.smallText}> *Uwaga podane ceny nie s cenami ostateczymi. Aby uzyska konkretn
                    wycen prosimy o kontak mailowy lub telefoniczny wszelkie niezbdne
                    dane podane s w zakadce kontakt </p>
        </div>
      </div>
    )
  }
  export default VIDEO;