import css from "../../styles/foto.module.scss"
import foto1 from "../../images/offer/FotoContent1.jpg";
import foto2 from "../../images/offer/FotoContent2.png";
import foto3 from "../../images/offer/FotoContent3.png";

const FOTO = () => {
    return (
      <div>
      <div className={css.title}>
        <p>
          <b>CUBE MEDIA</b> OFERTA FOTO
        </p>
      </div>
      <div className={css.content}>
          <div className={css.fotoWrapper}>
            <img src={foto1} className={css.foto}/>
            <div className={css.description}> 
              <p className={css.descriptionHeader}>Sesje motoryzacyjne od 250z</p>
              <p>kompleksowa sesja obejmujca:</p>
              <p>-kilka lokalizacji</p>
              <p>-min. 25 zdj w najwyszej moliwej jakoci</p>
              <p>-szybk i profesjonalna obrbk</p>
              <p>-zdjcia pogldowe dla ewentualnych poprawek ju na drugi dzie </p>
            </div>
          </div>
          <div className={css.fotoWrapper}>
            <img src={foto2} className={css.foto}/>
            <div className={css.description}> 
              <p className={css.descriptionHeader}>Realizacje eventowe od 500z </p>
              <p>kompleksowa realizacja zawierajca:</p>
              <p>-pakiet od 100 gotowych zdj z wydarzenia</p>
              <p>-pierwsze zdjcia gotowe do publikacji w 15 minut po koncercie</p>
              <p>-rnorodne ujcia ukazujce cay event (artyci, publika, backstage itd.)</p>
            </div>
          </div>
          <div className={css.fotoWrapper}>
            <img src={foto3} className={css.foto}/>
            <div className={css.description}> 
              <p className={css.descriptionHeader}>Sesje produktowe studyjny/plenerowe od 100z/produkt</p>
              <p>oferujemy:</p>
              <p>-dokadn postprodukcj </p>
              <p>-ujcia dowolnego produktu na dowolnym tle </p>
              <p>-packshoty oraz sceny</p>
              <p>-od jednego do kilku uj jednego przedmiotu do wyboru</p>
            </div>
          </div>
      </div>
  </div>
    )
  }
  export default FOTO;