import css from "../styles/team.module.scss";
function Team() {
  return <div className={css.team}>
        <div className={css.photoContainer1}>
        <div className={css.photo1}></div>
        <div className={css.photo2}></div>
            <span className={css.photoText}>
            <h5>MACIEJ KOWALONEK</h5> Fotograf z trzy letnim dowiadczeniem w reportau.
             Specjalizuje si w fotografii produktowej. Mionik fotografii
              koncertowej oraz twrca teledyskw hip-hopowych. Uwielbia dobrze
               zaparzon yerb.
            </span>
        </div>
        <div className={css.photoContainer2}>
        <div className={css.photo3}></div>
        <div className={css.photo4}></div>
            <span className={css.photoText}>
            <h5>BARTOSZ KAKOWSKI</h5> Zapalony filmowiec oraz fotograf. W swojej pracy
             jest w stanie zauway i ukaza najmniejsze detale. Pasjonuje si
              fotografi motoryzacj oraz koncertow. Kocha dwik gonego wydechu
               oraz zapach palonej gumy
            </span>
        </div>
        <div className={css.photoContainer3}>
        <div className={css.photo5}></div>
        <div className={css.photo6}></div>
            <span className={css.photoText}>
            <h5>LESAW KOPECKI</h5> Minimalista, mionik dobrych smakw, esteta.
             Znany przede wszystkim jako fotograf koncertowy. Zajmujcy
              si si szeroko pojtym tworzeniem treci od zdj, poprzez
               filmy, a po copywriting.
            </span>
        </div>
        <div className={css.photoContainer4}>
        <div className={css.photo7}></div>
        <div className={css.photo8}></div>
            <span className={css.photoText}>
            <h5>KACPER MIKUA</h5> Zapalony fotograf specjalizujcy si w fotografii
             studyjnej oraz koncertowej. Lubi dobr muzyk oraz szybkie auta.
              Okazjonalnie pomaga przy tworzeniu oraz montau teledyskw
            </span>
        </div>
        <div className={css.photoContainer5}>
        <div className={css.photo9}></div>
        <div className={css.photo10}></div>
            <span className={css.photoText}>
            <h5>MATEUSZ KUP</h5> Gwny filmowiec i montaysta w zespole. Jest autorem
             dziesitek niezalenych filmw od komercyjnych poprzez fabuy
              koczc na editach sportowych. Postprodukcja kolorw w video
               nie jest mu straszna. Hobbystycznie jedzi wyczynowo na rowerze.
            </span>
        </div>
        <div className={css.photoContainer6}>
            <div className={css.photo11}></div>
            <div className={css.photo12}></div>
            <span className={css.photoText}>
                <h5>KINGA DEJEWSKA</h5>Copywriterka i specjalistka od social mediw.
                W swoich fotografiach kocha zatrzymywa emocjonalne momenty.
                Uwielbia robi zdjcia portretowe oraz koncertowe. Sport to jej drugie ycie
            </span>
        </div>
        <div className={css.main}>
            <span className={css.textSpan}>
                Nasz zesp skada si z modych, prnie dziaajcych twrcw 
                produkujcych materiay na najwyszym poziomie. Szeciu 
                indywidualistw poczyo siy aby stworzy Cube Media.
            </span>
        </div>
  </div>;
}

export default Team;